/*
 *- A lazy-loading progressive image system
 *- Credits: https://github.com/craigbuckler/progressive-image.js
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

.progressive {
  position: relative;
  display: block;
  overflow: hidden;
  outline: none;


  img {
    display: block;
    width: 100%;
    max-width: none;
    height: auto;
    border: 0 none;

    &.preview {
      filter: blur(2vw);
      transform: scale(1.05); }

    &.reveal {
      position: absolute;
      left: 0;
      top: 0;
      will-change: transform, opacity;
      animation: progressiveReveal 1s ease-out;
      z-index: 0; } } }

@keyframes progressiveReveal {
  0% {
    transform: scale(1.05);
    opacity: 0; }

  100% {
    transform: scale(1);
    opacity: 1; } }
