/*
 ** Tipography
 *
 * $grey050:    #fafafa
 * $grey500:    #9e9e9e
 * $grey700:    #616161
 * $text:       #222222
 * $clean:      #dfdce3
 * $dark:       #888888
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 2rem;
  @include font(monospace, false, false, 600, false); }

h1 {
  @include font(false, 4.0rem, false, false, 1.2);
  letter-spacing: -.1rem; }

h2 {
  @include font(false, 3.6rem, false, false, 1.25);
  letter-spacing: -.1rem; }

h3 {
  @include font(false, 3.0rem, false, false, 1.3);
  letter-spacing: -.1rem; }

h4 {
  @include font(false, 2.4rem, false, false, 1.35);
  letter-spacing: -.08rem; }

h5 {
  @include font(false, 1.8rem, false, false, 1.5);
  letter-spacing: -.05rem; }

h6 {
  @include font(false, 1.5rem, false, false, 1.6);
  letter-spacing: 0; }

/*
 *- NOTE: scala aurea
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */
@include mediaQ(exsmall) {
  h1 {
    @include font(false, 5.0rem, false, false, 1.1); }
  h2 {
    @include font(false, 4.2rem, false, false, 1.18); }
  h3 {
    @include font(false, 3.1rem, false, false, 1.34); }
  h4 {
    @include font(false, 2.6rem, false, false, 1.19); }
  h5 {
    @include font(false, 1.9rem, false, false, 1.36); }
  h6 {
    @include font(false, 1.6rem, false, false, 1.19); } }


p {
  /* from main: +font($systemFF,1.6rem,$text,400,1.6) */
  @include font(sans-serif, false, false, false, false);
  margin-top: 0;
  hyphens: auto; }

@include mediaQ(exsmall) {
  p {
    @include font(false, 1.8rem, false, false, 1.8); } }


span#datiLoadedImage {
  @include position(fixed, 5rem, 0, null, 2rem);
  @include font(false, 1.8rem, #FFFFFF, 500, 1.2);
  //position: relative
  h5, h6 {
    line-height: 1.1;
    margin-bottom: 0; }
  h6 {
    line-height: 1.4; } }
