/*
 ** Media
 *
 * $grey050:    #fafafa
 * $grey500:    #9e9e9e
 * $grey700:    #616161
 * $text:       #222222
 * $clean:      #dfdce3
 * $dark:       #888888
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

img:not([src]):not([srcset]) {
  visibility: hidden; }

@-moz-document url-prefix() {
  img:-moz-loading {
    visibility: hidden; } }

img,
video,
object {
  max-width: 100%;
  height: auto;
  border: none;
  outline: none; }

figure {
  max-width: 100%;
  margin: auto; }

figcaption {
  margin: 2rem auto;
  text-align: center;
  @include font(false, false, false, false, 3);
  border: 1px solid $borderColor;
  background-color: $btnBgkColor; }
