/* Headere and Navigation
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

//.siteHeader
//.wrapperHeader


#header {
  @include position(fixed, 0, null, auto, 0);
  background-color: #1F2224;
  width: 100%;
  height: 5rem;
  line-height: 5rem;
  padding: 0 1.6rem;
  user-select: none;
  z-index: 10000 + 2;

  a.idPrivacyFooter {
    @include font(false, false,#4c4c4c,false,false);
    img {
      vertical-align: middle;
      line-height: 30px;
      @include size(3rem);
      display: inline;
      margin: 0 1rem 0.5rem 1rem; }
    &:hover {
      @include font(false, false,#f08dc4,false,false); } }

  h1 {
    display: inline-block;
    @include font(false, 1.5rem, #FFFFFF, 400, 1);
    text-transform: none;
    vertical-align: middle;
    margin: 0;
    a {
      @include font(false, false, inherit, false, false);
      border: 0;
      &:hover {
        color: inherit !important; }
      strong {
        @include font(false, false, red, 600, false);
        text-transform: uppercase; } } }

  nav {
    @include position(absolute, 0, 0, null, null);
    a {
      display: inline-block;
      @include font(false, 1.3rem, #FFFFFF, false, 1);
      text-transform: uppercase;
      border: 0;
      letter-spacing: .16rem;
      padding: 0 1.856rem;
      > span {
        display: inline-block;
        vertical-align: middle; } } } }

@include mediaQ(small) {
  #header {
    @include position(fixed, auto, null, 0, 0); }
  .sectionTerms {
    padding: 2rem 2rem 7rem; } }
