.lity {
  z-index: 9990;
  @include position(fixed,0,0,0,0);
  white-space: nowrap;
  background: $grey050;
  background: rgba(250, 250, 250, 0.4);
  outline: none !important;
  opacity: 0;
  transition: opacity 0.3s ease;

  &.lity-opened {
    opacity: 1; }

  &.lity-closed {
    opacity: 0; }

  //*
 }  //box-sizing: border-box

.lity-wrap {
  z-index: 9990;
  @include position(fixed,0,0,0,0);
  text-align: center;
  outline: none !important;
  &:before {
    content: '';
    display: inline-block;
    height: 100%;
    vertical-align: middle;
    margin-right: -0.25em; } }

.lity-loader {
  @include position(absolute, 50%, null, null, null);
  @include font(false, 1.4rem, #FFFFFF,false,false);
  text-align: center;
  margin-top: -0.8em;
  width: 100%;
  opacity: 0;
  transition: opacity 0.3s ease;
  z-index: 9991; }

.lity-loading .lity-loader {
  opacity: 1; }

.lity-container {
  z-index: 9992;
  position: relative;
  text-align: left;
  vertical-align: middle;
  display: inline-block;
  white-space: normal;
  max-width: 100%;
  max-height: 100%;
  outline: none !important; }

.lity-content {
  z-index: 9993;
  width: 100%;
  transform: scale(1);
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease, -o-transform 0.3s ease;
  //> span
  //@include position(fixed, 5rem, 0, null, 2rem)
 }  //@include font(false, 1.8rem, #FFFFFF, 500, 1.2)



.lity-loading .lity-content, .lity-closed .lity-content {
  transform: scale(0.8); }

.lity-content:after {
  display: block;
  @include position(absolute,0,0,0,0);
  content: '';
  width: auto;
  height: auto;
  z-index: -1;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.6); }

.lity-close {
  @include size(45px);
  @include position(fixed,0,0,null,null);
  @include font(false,45px,#FFFFFF,false,1);
  font-style: normal;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.6);
  text-decoration: none;
  text-align: center;
  box-shadow: none;
  background: none;
  outline: none;
  border: 0;
  padding: 0;
  cursor: pointer;
  z-index: 9994;

  &::-moz-focus-inner {
    border: 0;
    padding: 0; }

  &:hover, &:focus, &:active, &:visited {
    @include font(false,45px,#FFFFFF,false,1);
    font-style: normal;
    text-shadow: 0 1px 2px rgba(0, 0, 0, 0.6);
    text-decoration: none;
    text-align: center;
    padding: 0;
    border: 0;
    background: none;
    outline: none;
    box-shadow: none; }

  &:active {
    top: 1px; } }

/* Image */

.lity-image img {
  max-width: 100%;
  display: block;
  line-height: 0;
  border: 0; }

.lity-hide {
  display: none; }
