/*
 ** Links
 *
 * $grey050:    #fafafa
 * $grey500:    #9e9e9e
 * $grey700:    #616161
 * $text:       #222222
 * $clean:      #dfdce3
 * $dark:       #888888
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

a:not([class]) {
  background-color: transparent;
  -webkit-text-decoration-skip: objects;
  text-decoration-skip-ink: auto;
  @include font(sans-serif,false,false,600,false);
  text-decoration: none;
  &:hover {}
  &:focus,
  &:active,
  &:visited {
    @include font(false,false,false,false,false);
    text-decoration: none;
    outline-width: 0; } }
