/* Footer
 *
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

#footer {}


.siteFooter {
  @include font(false, false, #FFFFFF, false, false);
  text-align: left;
  hyphens: auto;
 }  //.wrapperFooter

p {
  color: #a0a0a1; }

span.copyright {
  @include font(false, 1.3rem, #a0a0a1, 600, 1);
  margin: 2rem auto;
  a {
    margin-left: 2rem; } }

.panel {
  @include position(fixed, calc(5rem - 1px), null, auto, 0);
  max-height: calc(80vh - 5rem);
  padding: 0 2.5rem;
  transform: translateY(-100vh);
  transition: transform 0.5s ease;
  background-color: rgba(37, 40, 42, .9);
  overflow-y: auto;
  width: 100%;
  visibility: visible;
  z-index: 10000;
  &.active {
    transform: translateY(0); }
  > .closer {
    @include position(absolute, 0,0,null,null);
    display: block;
    @include size(7rem);
    transition: opacity 0.2s ease-in-out;
    background-image: url('#{$closer}');
    background-position: top right;
    background-repeat: no-repeat;
    background-size: 5rem;
    cursor: pointer;
    opacity: 0.25;
    z-index: 2;
    &:hover {
      opacity: 1.0; } }
  > .inner {
    max-width: 100%;
    margin: 0 auto;
    &.split {
      display: flex;
      flex-direction: row;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;
      > div {
        width: 98%; } } } }

@media screen and (min-width: 551px) and (max-width: 552px) {
  .panel {
    visibility: hidden; } }

@include mediaQ(small) {
  .avatar {
    display: flex;
    align-items: baseline; }
  .panel {
    @include position(fixed, auto, null,calc(5rem - 1px), 0);
    visibility: visible;
    transform: translateY(100vh);
    > .inner {
      width: 95rem;

      &.split {
        justify-content: space-between;
        > div {
          width: 49%; } } } } }
