.pageWrapper {
  margin: 0;
  padding: 0;
  position: relative;
  //background-color: #1F2224
 }  //z-index: 9000

.sectionTerms {
  padding: 7rem 2rem 0; }
@include mediaQ(small) {
  .sectionTerms {
    padding: 2rem 2rem 7rem; } }

.photos {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: flex-start;
  align-content: stretch;
  margin: 5rem 0 0 0;
  padding: 0;
  position: relative;

  a {
    display: block;
    float: left;
    flex: 0 0 auto;
    //background-color: #fff
    width: 100%;
    height: 100%;
    position: relative; } }

@media screen and (orientation: landscape) and (min-width: 568px) {
  //.photos img
  .photos a {
    width: calc(100%/3);
    height: calc(100%/3); } }

@media screen and (orientation: portrait) and (min-width: 414px) {
  .photos a {
    width: calc(100%/2);
    height: calc(100%/2); } }

@media screen and (min-width: 736px) {
  .photos a {
    width: calc(100%/3);
    height: calc(100%/3); } }

@media screen and (min-width: 768px) and (max-width: 1024px) {
  .photos {
    margin: 0 0 5rem 0; }
  .photos a {
    width: calc(100%/4);
    height: calc(100%/4); } }

@media screen and (min-width: 1024px) {
  .photos {
    margin: 0 0 5rem 0; }
  .photos a {
    width: calc(100%/6);
    height: calc(100%/6); } }

@media screen and (min-width: 1280px) {
  .photos a {
    width: calc(100%/8);
    height: calc(100%/8); } }
