/*
 ** Utilities
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

.tal {
  text-align: left; }
.tac {
  text-align: center;
  margin-left: auto;
  margin-right: auto; }
.tar {
  text-align: right; }
.taj {
  text-align: justify; }

.clearfix {
  @include clearfix; }

.sva {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: flex-start;
  align-items: center; }

.red {
  color: red; }
.block {
  display: block; }

.flow {
  --flow-space: 1em; }

.flow > * + * {
  margin-top: 1em;
  margin-top: var(--flow-space); }
