/*
 ** Mixins
 *–––––––––––––––––––––––––––––––––––––––––––––––––– */

@mixin clearfix {
  &:after {
    content: "";
    display: table;
    clear: both; } }

@mixin font($ff: false, $size: false, $colour: false, $weight: false, $lh: false) {
  @if $ff {
    font-family: $ff; }
  @if $size {
    font-size: $size; }
  @if $colour {
    color: $colour; }
  @if $weight {
    font-weight: $weight; }
  @if $lh {
    line-height: $lh; } }

@mixin backgroundImage($color: false, $blend: false) {
  background: {
    repeat: no-repeat;
    size: cover;
    position: 50% 50%;
    @if $color {
      color: $color; }
    @if $blend {
      blend-mode: $blend; } } }

@mixin position($position, $top: null, $right: null, $bottom: null, $left: null) {
  position: $position;
  top: $top;
  right: $right;
  bottom: $bottom;
  left: $left; }

@mixin size($size: null) {
  width: $size;
  height: $size; }

@mixin mediaQ($val) {
  @if $val == exsmall {
    @media screen and (min-width: 551px) {
      @content; } }
  @else if $val == small {
    @media screen and (min-width: 768px) {
      @content; } }
  @else if $val == medium {
    @media screen and (min-width: 1025px) {
      @content; } }
  @else if $val == large {
    @media screen and (min-width: 1224px) {
      @content; } }
  @else {
    @media screen and (min-width: $val) {
      @content; } } }

/*
 ** Extentions
 *–––––––––––––––––––––––––––––––––––––––––––––––––– */

%textJ {
  text-align: justify;
  word-wrap: break-word;
  word-break: break-all;
  hyphens: auto; }

%text {
  text-align: justify;
  word-break: keep-all;
  hyphens: auto; }

%titoli {
  text-transform: uppercase;
  text-align: center;
  white-space: nowrap; }
