/*
 ** Base styles
 *
 * $borderColor: $dark
 * $btnBgkColor: $grey050
 *
 *- NOTE: html is set to 62.5% so that all the REM measurements throughout Skeleton
 *- are based on 10px sizing. So basically 1.5rem = 15px :)
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

$grey050:    #fafafa;
$grey500:    #9e9e9e;
$grey700:    #616161;
$white :     #ffffff;
$black :     #000000;
$text:       #222222;
$clean:      #dfdce3;
$dark:       #888888;

$borderColor: $dark;
$btnBgkColor: $grey050;

$systemFF:  Raleway, HelveticaNeue, Helvetica, Arial, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen-Sans, Ubuntu, Cantarell, 'Helvetica Neue', sans-serif;

*,
*::before,
*::after {
  box-sizing: border-box;
  box-sizing: inherit; }

html {
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-variant-ligatures: common-ligatures;
  -webkit-tap-highlight-color: transparent;
  font-kerning: normal;
  font-family: sans-serif, serif;
  font-size: 62.5%;
  box-sizing: border-box; }

html.no-js {
  body:before {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    content: "No javascript? No Page!"; } }

body {
  -webkit-text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
  font-kerning: normal;
  font-variant-ligatures: common-ligatures;
  text-rendering: optimizeLegibility;
  overflow-x: hidden;
  /* currently ems cause chrome bug misinterpreting rems on body element */
  @include font($systemFF,1.6rem,$text,400,1.6);
  margin: 0; }

/* margins
 *–––––––––––––––––––––––––––––––––––––––––––––––––– */

button, .button {
  margin-bottom: 1rem; }

input, textarea, select, fieldset {
  margin-bottom: 1.5rem; }

pre, blockquote, dl, figure, table, p, ul, ol, form {
  margin-bottom: 2.5rem; }

/* hr
 *–––––––––––––––––––––––––––––––––––––––––––––––––– */

hr {
  margin: 64px auto 0;
  max-width: 70%;
  border : 0;
  height: 1px;
  background-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0.75), rgba(0, 0, 0, 0)); }
