/*
 ** Lists
 * ––––––––––––––––––––––––––––––––––––––––––––––––– */

ul[class] {
  list-style: none;
  padding: 0; }

li {
  margin-bottom: 1rem; }

ul.webLinks {
  width: 65%;
  margin: 2rem 0;
  padding: 2rem;
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  align-items: center;
  background-color: rgba(160, 160, 161, .2);
  border-radius: 5px;
  li {
    display: inline-block;
    background-color: rgba(36, 38, 41, 0.2);
    height: 35px;
    line-height: 35px;
    vertical-align: middle;
    margin: 0; } }
